* {
    margin: 0; padding: 0;
    box-sizing: border-box;
    /*font-family: ;*/
}

html{
    font-size: 62.5%;
}

.App{
    background-color: #333;
    height: 100vh;
    width: 100%;
    padding: 2rem;
    border: 1px solid black;
}

.title{
    font-size: 4rem;
    text-align: center;
    margin: 2rem 0;
}

.input{
    max-width:45rem;
    margin-bottom: 2rem;
    margin: 0 auto;
    text-align: center;
    position: relative;
}

.input-text{
   width: 100%; 
   padding: 1rem;
   border-radius: 5rem;
   font-size: 2.5rem;
   border: none;
}

.input-button{
    position: absolute;
    right: 1rem;
    top: 0.5rem;
    height: 4rem;
    width: 4rem;
    border-radius: 50%;
    transition: 0.1s linear;
    border: 0.1rem solid black;
}

.input-button:hover{
    cursor: pointer;
}

.input-button:active{
    scale: 0.8;
}


/* media queries */

@media (max-width:768px){
    html{
        font-size: 55%;
    }
}

@media (max-width:500px){
    html{
        font-size: 50%;
    }
}



